
import {defineComponent} from "vue";
import store from "@/store/store"

export default defineComponent({
  emits: ["idSent"],
  name: "IDsList",
  props: {
    ids: {
      type: Array<string>,
      required: true
    }
  },
  setup(_, {emit}) {
    const emitIdsSent = (ids: string[]) => {
      emit('idSent', ids);}
    return {
      store,
      emitIdsSent
    }
  }
})
